import moment from "moment"
import { Link } from 'react-router-dom'
import { Badge, Col } from "reactstrap"

// ** Table columns
const statusObj = [
  'light-warning',
  'light-success',
  'light-secondary'
]

const priorityOptions = ['Low', 'Medium', 'High']
const statusOptions = ['To Do', 'In progress', 'Completed', 'On Hold', 'Cancelled', 'Sent to Review', 'Request Changes']
const dateFormat = (value) => {

  return moment.unix(value).format("MMM DD, YYYY")

}

export const columns = [
  {
    name: '#',
    sortable: false,
    minWidth: '80px',
    selector: row => row.uniqueidentity,
    cell: row => <Col className="text-primary cursor-pointer" tag={Link}
      to={`/task/view/${row.id}`}>{row.uniqueidentity || 0}</Col>
  },
  {
    name: 'Service Name',
    minWidth: '230px',
    sortable: false,
    selector: row => row.servicename,
    cell: row => <span>{row.servicename || 0}</span>
  },

  {
    name: 'End Date',
    sortable: false,
    minWidth: '180px',
    selector: row => row.enddate,
    cell: row => <span>{dateFormat(row.enddate)}</span>
  },
  {
    name: 'Priority',
    minWidth: '230px',
    sortable: true,
    sortField: 'priority',
    selector: row => row.priority,
    cell: row => <span className='text-capitalize'>{priorityOptions[row.priority]}</span>
  },
  {
    name: 'Status',
    minWidth: '138px',
    sortable: true,
    sortField: 'taskstatus',
    selector: row => row.taskstatus,
    cell: row => (
      <Badge className='text-capitalize' color={statusObj[row.taskstatus]} pill>
        {statusOptions[row.taskstatus]}
      </Badge>
    )
  }
]
